import s from "../style/user.module.sass";
import { FallbackImg } from "./FallbackImg";

export function UserEntry(props) {
    var u = props.usr;
    return (<div className={s.userEntry}> apiUrl
        <div className={s.pic}><FallbackImg src={u.avatarUrl}></FallbackImg></div>
        <div className={s.body}>
            <div><b>{u.clearname||u.username}</b></div>
            <div><i style={{color:"#888"}}>@{u.username}</i></div>
        	<div><pre style={{fontFamily:"inherit"}}>{u.description||"..."}</pre></div>
            <div><i style={{color:"#888"}}>id: {u.id}</i></div>
        </div>
    </div>);
}